export function startupServiceFactory(startupService: NLocalStorageService) {
  const res = () => {
    // console.log('module before initstorage call');
    return startupService.initStorage();

  }
  return res;
}


@NgModule({
  imports: [
    CommonModule,
    HttpClientModule,
    RouterModule,
    HttpModule,
    NgForageModule.forRoot()
    // BrowserModule
  ],
  providers: [
    {
      provide: HTTP_INTERCEPTORS,
      useClass: NHttpService,
      multi: true
    },
    {
      // Provider for APP_INITIALIZER
      provide: APP_INITIALIZER,
      useFactory: startupServiceFactory,
      deps: [NLocalStorageService],
      multi: true
    },
    NAuthGuardService,
    NDataModelService,
    // NDataSourceService,
    NHttpService,
    NHTTPLoaderService,
    NLocalStorageService,
    NLoginService,
    NLogoutService,
    NNotificationService,
    NSnackbarService,
    NPubSubService,
    //     NSystemService,
    NSessionStorageService,
    NTokenService
  ],
  declarations: [
    SnackComponent,
    SnackbarComponent
  ],
  exports: [
    SnackComponent,
    SnackbarComponent
  ]

})
export class NeutrinosSeedServicesModule {

}
